html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

.content-box-component {
  box-sizing: content-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Lucida Console", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-style: normal;
  color: #788891; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden; }
